  opacity: 1; }

#menuBurg span:nth-child(3) {
  top: 90%;
  width: 14px;
  opacity: 1; }

#menuBurg.menu-burger-open span:nth-child(1) {
  width: 27px;
  transform: translateX(-4.5px) translateY(8px) rotate(45deg); }

#menuBurg.menu-burger-open span:nth-child(2) {
  width: 0%;
