@font-face {
  font-family: 'TTLakes';
  src: url(./fonts/TTLakes-Regular.ttf) format("truetype");
  font-weight: normal;
  font-style: normal; }

html {
  box-sizing: border-box; }

*,
*::after,
*::before {
  box-sizing: inherit; }

body {
  font-family: 'TTLakes';
  min-width: 320px;
  overflow-x: hidden;
  margin: 0; }

header {
  border-bottom: 1px solid #41f6d7; }

main {
  background-color: #f8f8f8; }

.main__hr_highlight {
  width: 4px;
  height: 40px;
  border: 0;
  background: #41f6d7;
  transform: matrix(-1, 0, 0, 1, 0, 0);
  border-radius: 10px 0% 0% 10px;
  margin-left: 0px;
  margin-top: 0px;
  flex: 0 0 4px; }

.active-string {
  content: '';
  opacity: 1;
  position: absolute;
  width: 4px;
  height: 30px;
  border: 0;
  background: #41f6d7;
  transform: matrix(-1, 0, 0, 1, 0, 0);
  border-radius: 10px 0% 0% 10px;
  margin-left: -16px;
  margin-top: -3px; }

.main__article_container {
  display: flex;
  padding-top: 24px;
  height: 40px;
  justify-content: space-between; }

.main__article_hr_text_container {
  display: flex; }

.main__article__text {
  font-size: 28px;
  line-height: 40px;
  letter-spacing: -0.6px;
  color: #1b1c21;
  font-weight: bold;
  width: 288px;
  height: 40px;
  margin-left: 12px;
  margin-top: 0px;
  vertical-align: center; }

.main__article_button_container {
  display: none;
  white-space: nowrap;
  text-decoration: none; }

.main__article_button_container a {
  color: #1b1c21;
  font-weight: 500;
  white-space: nowrap;
  text-decoration: none; }

.article__button_container {
  display: flex;
  align-items: center;
  font-size: 16px;
  line-height: 40px; }

.button__container_a_text {
  margin-right: 8px;
  padding-right: 50px; }

.read-more-checker {
  /*спрятал чекер с галочкой*/
  opacity: 0;
  position: absolute; }

br {
  margin-top: 28px; }

.main__p_text {
  min-width: 288px;
  max-width: 540px;
  max-height: 90px;
  overflow: hidden;
  margin-left: 16px;
  margin-right: 16px;
  padding: 0px;
  font-size: 14px;
  line-height: 18px; }

.text {
  max-height: 0px; }

.read-more-checker:checked ~ .text {
  /*применяется для всех элементов с классом text, после перевода в статус checked элемента с классом read-more-checker*/
  max-height: none; }

.read-more-button {
  margin-top: 16px;
  margin-left: 24px; }

.main__button_text {
  font-size: 16px;
  line-height: 24px;
  color: #1b1c21;
  padding-left: 16px;
  font-weight: 500; }

/*main text end*/
/*main img begin*/
.main__img img {
  width: 100%;
  object-fit: cover;
  object-position: bottom; }

/*main img end*/
.gradient {
  width: 40px;
  height: 72px;
  position: absolute;
  z-index: 3;
  background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, white 70%, white 100%);
  right: 0;
  top: 0;
  display: none; }

.title-page {
  font-family: 'TT Lakes';
  text-transform: uppercase;
  font-weight: 500;
  font-size: 16px;
  color: #7e7e82;
  padding: 16px;
  margin-bottom: 32px; }

.header__container {
  padding-left: 16px;
  padding-right: 16px; }

.header__menu {
  height: 88px;
  display: flex;
  justify-content: space-between; }

.header__menu_container_burger-logo-hr {
  display: flex;
  align-items: center; }

.header__menu_container_button-hr {
  display: flex;
  align-items: center; }

.header__menu_container_button_hidden,
.header__hr_second {
  display: none; }

.header__menu_button-burger,
.header__menu_logo {
  margin-right: 16.73px; }

#call,
#chat,
#profile,
#repair,
#second-hr {
  margin-right: 16px; }

.header__hr,
.header__hr_second {
  background: #eaeaea;
  border-radius: 1px;
  border: 0;
  width: 2px;
  height: 32px; }

/*Логотип css*/
.header__menu_logo {
  background-image: url(./static/Group.svg);
  background-repeat: no-repeat;
  width: 98px;
  height: 52px;
  position: relative;
  margin-left: 17px;
  z-index: 2; }

/*menu begin*/
.main__nav {
  display: flex;
  overflow: auto;
  padding: 40px; }

.main__nav::-webkit-scrollbar {
  display: none; }

.main__nav_text {
  font-size: 16px;
  line-height: 24px; }

.main__nav a {
  color: #7e7e82;
  text-align: start;
  /* background-color: $white; */
  border-radius: 6px;
  padding: 8px 9.3px 8px 16.54px;
  border-bottom: 0px;
  margin: 0px;
  /* outline: 2px solid $turq; */
  transition: 0.5s;
  white-space: nowrap;
  text-decoration: none; }

.main__nav a:active,
.main__nav a:focus {
  outline: 2px solid #41f6d7;
  background-color: #fff; }

/*menu end*/
/*Дроп меню*/
.header__menu_drop-menu {
  display: block;
  position: absolute;
  width: 320px;
  top: 0%;
  left: 0%;
  transform: translateY(88px);
  background-color: #fff;
  box-shadow: 16px 0px 24px -13px rgba(34, 60, 80, 0.18);
  z-index: 2;
  opacity: 0;
  transform: scaleY(0);
  transform-origin: 0 0;
  transition: 0.5s ease-in-out; }

.visibility {
  opacity: 1;
  transform: scaleY(1); }

.menu-burger {
  z-index: 2; }

.drop-menu__list {
  list-style-type: none;
  margin-top: 120px; }

.drop-menu__list > li {
  position: relative;
  margin-bottom: 32px; }

.drop-menu__list a {
  text-decoration: none;
  color: #1b1c21;
  padding: 5px; }

.drop-menu__list a:focus {
  /* background: url(/highlight/highlight.svg) no-repeat; */
  /*нужно поправить!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!*/
  background-position: left;
  opacity: 0.5; }

.drop-menu__list a:hover {
  color: #7e7e82; }

.drop-menu__search-btn {
  position: absolute;
  transform: translateX(250px) translateY(23px); }

/*Footer drop menu*/
.drop-menu__footer {
  display: block;
  margin-left: 24px;
  margin-top: 268px; }

.drop-menu__footer a {
  display: block;
  text-decoration: none;
  color: #1b1c21; }

.footer__button-container {
  display: flex; }

.footer__lang-box {
  display: flex;
  margin-top: 40px;
  font-size: 16px; }

.footer__lang-box a {
  margin-right: 7px; }

.footer__lang-box a:focus {
  opacity: 0.5; }

.drop-menu__email,
.drop-menu__phone {
  margin-top: 16px; }

.drop-menu__email {
  font-size: 16px; }

.drop-menu__phone {
  font-size: 24px;
  font-weight: bold; }

/* Кнопки css */
.menu-btn a {
  display: block;
  width: 40px;
  height: 40px;
  background-color: #ff3e79;
  border-radius: 50%;
  position: relative; }

.menu-btn #menuBurg {
  z-index: 3; }

#menuBurg span {
  display: block;
  content: '';
  position: absolute;
  margin-top: -8px;
  left: 50%;
  margin-left: -9px;
  width: 18px;
  height: 2px;
  background-color: #fff;
  border-radius: 1px;
  transition: 0.5s;
  opacity: 0; }

/* media begin*/
@media (max-width: 400px) {
  .swiper {
    width: 300px; }
  .swiper-wrapper {
    flex-wrap: nowrap; }
  .show-more-btn {
    display: none; }
  .gradient {
    display: block; } }

@media screen and (min-width: 767px) and (max-width: 1118px) {
  .header__menu_container_button_hidden,
  .header__hr_second {
    display: flex; }
  .header__menu_container_burger-logo-hr :nth-child(2) {
    order: 2; }
  .header__hr {
    margin-right: 20.73px; }
  .main__p_img_container {
    display: flex;
    justify-content: center; }
  .main__img img {
    min-width: 360px;
    min-height: 208px;
    border-radius: 2%;
    margin-right: 25px; }
  .header__container {
    padding-left: 24px;
    padding-right: 24px; }
  .text {
    max-height: 35px; }
  .main__p_text {
    min-width: 312px;
    max-width: 1000px;
    margin-left: 24px;
    margin-right: 48px; }
  .main__article__text {
    margin-left: 20px; } }

@media screen and (min-width: 1119px) {
  header {
    display: none; }
  .main__article__text {
    margin-left: 40px; }
  .main__article_button_container {
    display: flex;
    margin-right: 32px; }
  .main__p_img_container {
    display: flex;
    justify-content: center; }
  .main__img img {
    min-width: 531.35px;
    min-height: 307px;
    border-radius: 2%;
    margin-right: 28.65px; }
  .main__nav {
    flex-wrap: wrap; }
  /* .swiper {
    width: 1100px;
  } */ }

/*media end*/
