.swiper {
  width: 830px;
  margin-left: 32px !important;
  overflow: hidden;
  margin-bottom: 16px; }

.swiper-wrapper {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  background-color: inherit; }

.slide {
  width: 240px;
  height: 72px;
  display: flex;
  border: 1px solid #eaeaea;
  border-radius: 4px;
  background-color: #fff;
  flex-shrink: 0;
  align-items: center;
  margin: 0 32px 16px 0; }

.slide a {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.swiper-pagination {
  position: relative !important; }

.swiper-pagination-bullet {
  width: var(--swiper-pagination-bullet-width, var(--swiper-pagination-bullet-size, 12px)) !important;
  height: var(--swiper-pagination-bullet-height, var(--swiper-pagination-bullet-size, 12px)) !important;
  margin: 0 var(--swiper-pagination-bullet-horizontal-gap, 6px) !important; }

.swiper-pagination-bullet-active {
  --swiper-theme-color: #757575; }

.slide__img {
  width: 130px;
  height: 45px;
  background-repeat: no-repeat;
  background-position: 0 50%;
  margin-left: 16px; }

.slide__btn {
  width: 40px;
  height: 40px;
  background-image: url(./static/arrow_btn.svg);
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
  border: 2px solid #ff3e79;
  border-radius: 50%;
  margin-right: 16px; }
